* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1 {
    text-align: center;
}

.container-main{
    margin: 5vh 0 5vh 0;
}

.container-main .ui.container{
    margin-bottom: 35px;
}

main .ui.vertical.menu {
    float: left;
}

main .ui.grid>.column {
    width:190px !important;
    height:220px;
}

.ui .breadcrumb{
    margin-bottom: 20px;
    font-style: italic;
}
  
.pusher > .footer {
    flex: 1;
}

.ui.search > .results {
    height: 70vh;
    overflow: scroll;
}

/* .ui.header:first-child {
    margin-top: calc(2rem - 0.142857em);
} */

.ui.card.prod {
    height: 275px;
    width: 300px;
    overflow: hidden;
    box-shadow: 1px solid #1e0039;
    border: 1px solid #604d9e;
}

.ui.card.prod>.content{
    background-color: #604d9e;
    color:#e4d6f8
}

.ui.cards>.card.prod {
    box-shadow: none !important;
}

.ui.cards.checkout>.card {
    padding: 1em
}


.green-text{
    color: #40cd28;
}
.center-row{
    display: flex!important;
    justify-content: center!important;
    align-items: center!important;
}

.banner-slider img{
    height: 100%;
    width:100%
}

.slick-slider.banner-slider.slick-initialized{
    margin-bottom: 5vh;
}